body {
  margin: 0;
  padding: 0;
  font-family: 'Arial', sans-serif;
  background: linear-gradient(to right, #6a11cb, #2575fc);
  display: flex;
  justify-content: center;
  align-items: center;
  height: 100vh;
  color: white;
  animation: fadeIn 1.5s ease-in-out;
}

.App {
  text-align: center;
}

.container {
  background: rgba(255, 255, 255, 0.2);
  padding: 20px;
  border-radius: 10px;
  box-shadow: 0 4px 8px rgba(0, 0, 0, 0.2);
}

h1 {
  margin-bottom: 20px;
  animation: slideIn 1.1s ease-in-out;
}

.form {
  margin-bottom: 20px;
}

.input {
  padding: 10px;
  border: 2px solid transparent;
  border-radius: 5px;
  margin-right: 10px;
  transition: transform 0.2s ease, box-shadow 0.2s ease;
  outline: none;
  background: white;
  color: black;
  font-size: 16px;
  animation: fadeIn 1.5s ease-in-out;
}

.input:focus {
  transform: scale(1.03);
  box-shadow: 0 4px 20px rgba(255, 64, 129, 0.5);
}

.button {
  padding: 10px 20px;
  border: none;
  border-radius: 5px;
  background-color: #ff4081;
  color: white;
  cursor: pointer;
  transition: background-color 0.3s;
  font-size: 16px;
  animation: fadeIn 1.5s ease-in-out;
}

.button:hover {
  background-color: #ff79a5;
}

.error {
  color: #ff0000;
  margin-top: 10px;
  animation: fadeIn 1s ease-in-out;
}

@keyframes fadeIn {
  0% { opacity: 0; }
  100% { opacity: 1; }
}


@keyframes slideIn {
  0% { transform: translateY(-20px); opacity: 0; }
  100% { transform: translateY(0); opacity: 1; }
}